@import "rouge-base16-dark";
@import "default_colors";

$container-max-width: 1000px;

// default (light) colors
$body-background: #f3f3f3;
$body-foreground: #141518;
$header-background: white;
$header-foreground: #0079db;
$link-color: #0079db;
$blockquote-color: #0079db;
$blockquote-border: #666;

// dark mode colors
$body-background-dark: #202123;
$body-foreground-dark: white;
$header-background-dark: #0c0c0c;
$header-foreground-dark: rgb(69, 159, 248);
$link-color-dark: rgb(69, 159, 248);
$blockquote-color-dark: rgb(69, 159, 248);
$blockquote-border-dark: #aaa;

@mixin dark-mode {
  background: $body-background-dark;
  color: $body-foreground-dark;

  header {
    background: $header-background-dark
  }

  header h1 {
    color: $header-foreground-dark
  }

  body {
    background: rgba(0, 0, 0, 0.8)
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: $header-foreground-dark;
  }

  footer {
    background-color: $header-background-dark;
    color: $header-foreground-dark;
  }

  a {
    color: $link-color-dark;
  }

  blockquote {
    color: $blockquote-color-dark;
    border-left: 1px double $blockquote-border-dark;
  }

  .github {
    background-image: url("/assets/images/github-mark-dark.png");
    background-size: 100%;
  }

  .mines {
    background-image: url("/assets/images/mines-logo-dark.png");
    background-size: 100%;
  }

}

@mixin media-max-width($max-width) {
  @media (max-width: $max-width) {
    @content;
  }
}

body {
  margin: 0;
  padding: 0;
  background: $body-background; // url("../images/bkg.png") 0 0;
  color: $body-foreground;
  font-size: 16px;
  line-height: 1.5;
  font-family: Monaco, "Bitstream Vera Sans Mono", "Lucida Console", Terminal, monospace;
}

/* General & 'Reset' Stuff */

.container {
  width: 90%;
  max-width: $container-max-width;
  margin: 0 auto;
}

section {
  display: block;
  margin: 0 0 20px 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 0px;
}

li {
  line-height: 1.4;
}

footer {
  text-align: center;
  padding: 3px;
  background-color: $header-background;
  border-top: 1px double $terminal; //header;
  color: $header-foreground;
  margin-top: auto;
}

/* Header, <header>
   header   - container
   h1       - project name
   h2       - project description
*/

header {
  background: $header-background;
  width: 100%;
  border-bottom: 1px double $header-foreground; //header;
  padding: 20px 0;
  margin: 0 0 40px 0;
}

header h1 {
  font-size: 30px;
  line-height: 1.5;
  margin: 0 0 0 -40px;
  font-weight: bold;
  font-family: Monaco, "Bitstream Vera Sans Mono", "Lucida Console", Terminal, monospace;
  color: $header-foreground;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.1),
    0 0 5px rgba(181, 232, 83, 0.1),
    0 0 10px rgba(181, 232, 83, 0.1);
  letter-spacing: -1px;
  -webkit-font-smoothing: antialiased;

  @include media-max-width($container-max-width) {
    margin-left: 0;
  }
}


header h1:before {
  content: "";
  font-size: 24px;
}

header h2 {
  font-size: 18px;
  font-weight: 300;
  color: rgb(0, 0, 0);
}

#downloads .btn {
  display: inline-block;
  text-align: center;
  margin: 0;
}

/* Main Content
*/

#main_content {
  width: 100%;
  -webkit-font-smoothing: antialiased;
  // display: flex;
  min-height: 100vh;
  // flex-direction: column;
  // justify-content: flex-start;
}

section img {
  max-width: 100%
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  font-family: Monaco, "Bitstream Vera Sans Mono", "Lucida Console", Terminal, monospace;
  color: $header-foreground;
  letter-spacing: -0.03em;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.1),
    0 0 5px rgba(181, 232, 83, 0.1),
    0 0 10px rgba(181, 232, 83, 0.1);
}

#main_content h1 {
  font-size: 30px;
}

#main_content h2 {
  font-size: 24px;
}

#main_content h3 {
  font-size: 18px;
}

#main_content h4 {
  font-size: 14px;
}

#main_content h5 {
  font-size: 12px;
  text-transform: uppercase;
  margin: 0 0 5px 0;
}

#main_content h6 {
  font-size: 12px;
  text-transform: uppercase;
  color: #999;
  margin: 0 0 5px 0;
}

dt {
  font-style: italic;
  font-weight: bold;
}

// ul li {
//   list-style-image:url('../images/bullet.png');
// }

blockquote {
  color: $blockquote-color;
  padding-left: 10px;
  border-left: 1px double $blockquote-border;
}

pre {
  background: rgba(0, 0, 0, 0.9);
  border: 1px solid rgba(255, 255, 255, 0.15);
  padding: 10px;
  font-size: 16px;
  color: #b5e853;
  border-radius: 2px;
  word-wrap: normal;
  overflow: auto;
  overflow-y: hidden;
}

code.highlighter-rouge {
  background: rgba(0, 0, 0, 0.9);
  border: 1px solid rgba(255, 255, 255, 0.15);
  padding: 0px 3px;
  margin: 0px -3px;
  color: #aa759f;
  border-radius: 2px;
}

table {
  width: 100%;
  margin: 0 0 20px 0;
}

th {
  text-align: left;
  border-bottom: 1px double #b5e853;
  padding: 5px 10px;
}

td {
  padding: 5px 10px;
}

hr {
  height: 0;
  border: 0;
  border-bottom: 1px double #888888;
  color: #b5e853;
}

/* Buttons
*/

.btn {
  display: inline-block;
  background: -webkit-linear-gradient(top, rgba(40, 40, 40, 0.3), rgba(35, 35, 35, 0.3) 50%, rgba(10, 10, 10, 0.3) 50%, rgba(0, 0, 0, 0.3));
  padding: 8px 18px;
  border-radius: 50px;
  border: 2px solid rgba(0, 0, 0, 0.7);
  border-bottom: 2px solid rgba(0, 0, 0, 0.7);
  border-top: 2px solid rgba(0, 0, 0, 1);
  color: rgba(255, 255, 255, 0.8);
  font-family: Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 13px;
  text-decoration: none;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.75);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);
}

.btn:hover {
  background: -webkit-linear-gradient(top, rgba(40, 40, 40, 0.6), rgba(35, 35, 35, 0.6) 50%, rgba(10, 10, 10, 0.8) 50%, rgba(0, 0, 0, 0.8));
}

.btn .icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  margin: 1px 8px 0 0;
  float: left;
}

.btn-github .icon {
  opacity: 0.6;
  background: url("../images/blacktocat.png") 0 0 no-repeat;
}

/* Links
   a, a:hover, a:visited
*/

a {
  color: $link-color;
  text-shadow: 0 0 5px rgba(104, 182, 255, 0.5);
}

/* Clearfix */

.cf:before,
.cf:after {
  content: "";
  display: table;
}

.cf:after {
  clear: both;
}

.cf {
  zoom: 1;
}

#a-title {
  text-decoration: none;
}

.actual_content {
  font-family: Verdana;
  text-align: justify;
}

.github {
  background-image: url("/assets/images/github-mark.png");
  background-size: 100%;
}

.linkedin {
  background-image: url("/assets/images/ledin-logo.png");
  background-origin: content-box;
  background-size: 100%;
  background-clip: content-box;
}

.mines {
  background-image: url("../images/mines-logo.png");
  background-origin: content-box;
  background-size: 100%;
  background-clip: content-box;
}

figure {
  text-align: center;
}


.dark-mode {
  @include dark-mode
}